@import url(https://fonts.googleapis.com/css?family=Numans);
@import url(https://fonts.googleapis.com/css?family=Numans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.styledLink {
  color: rgb(139, 139, 139);
  cursor: pointer;
  text-decoration: none;
}
.styledLink h4{
  color: white;

}
.styledLink:hover{
  color: white;
  text-decoration: none;
}

#login .container {
    -webkit-align-content: center;
            align-content: center;
   
}
#login .background{
    background-image: url("http://www.landmarkhearing.com/wp-content/uploads/2018/06/concert-crowd.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    font-family: 'Numans', sans-serif;
    color: white;
}
#login .align-items-center {
    -webkit-align-items: center!important;
            align-items: center!important;
}

#login .d-flex {
    display: -webkit-flex!important;
    display: flex!important;
}

#login .card {
    width: 500px;
    height: 200px;
    margin-bottom: auto;
    background-color: rgba(129, 129, 129, 0.8) !important;
}

#login .card-header h1 {
    text-align: center;
    color: rgb(0, 0, 0);
}

#login .input-group-prepend span {
    background-color: rgb(0, 64, 255);
    width: 50px;
    color: white;
    border: 0 !important;
}

#login .login_btn {
    color: white;
    background-color: rgb(0, 64, 255);
    width: 100%;
}

#login .login_btn:hover {
    color: rgb(0, 64, 255);
    background-color: white;
}

#login .links {
    color: white;
}
#login .links a{
    color: rgb(0, 0, 0);
    margin-left: 5px;
}

#login .option {
    color: white;
}

#login .option button {
    width: 100px;
    font-size: 40px;
    margin-left: 10px;
    color: white;
    background-color: rgb(0, 64, 255);
}
#login .option button:hover {
    color: rgb(5, 26, 91);
    background-color: white;
}

#login #carta{
    margin-top: 20%;
}
#login  .links .link{
    color:rgb(0, 64, 255);
    text-decoration: underline;
}
#login .links .link:hover{
    cursor: pointer;
    color:white;
}
#login .login_btn .sptIcon{
    font-size: 20px;
}

/*Menu*/
#main .background{
    background-color: rgb(39, 39, 39);
    color: white;
    font-family: 'Lucida Console';
    width: 100%;
}
#main #sidebar-wrapper {
    min-height: 100vh;
    background-color: black;
    color: rgb(139, 139, 139);
    top: 0;
    padding: 5px;
}

#main #sidebar-wrapper .sidebar-heading:hover {
    color: rgb(255, 255, 255);
    text-shadow: 0 0 1px white;
    cursor: pointer;
}

#main #sidebar-wrapper .sidebar-heading {
    padding: 0.875rem 1.25rem;
    font-size: 1.2rem;
}

#main #sidebar-wrapper .list-group {
    width: 15rem;
    position: -webkit-sticky;
    position: sticky;
    top:0;
}

#main #wrapper.toggled #sidebar-wrapper {
    margin-left: 0;

}
#main .selected {
    border-left: 6px solid rgb(0, 89, 255);
    color: rgb(255, 255, 255);
    text-shadow: 0 0 1px white;
}
/*Home Page*/

#main .header {
    min-width: 100%;
    height: 120px;
    color: white;
    font-size: 6rem;
    display:block;
}


#main .content {
    padding-top: 4rem;
    font-size: 1.2rem;
}

#main .section-header {
    border-bottom: 0.5px solid white;
}
#main .section-content{
    font-size: 1rem;
    color: rgb(165, 162, 162);
}
#main .temp-image{
    height: 225px;
    width: 225px;
    color: rgb(0, 89, 255);
    padding-top: 4rem;
    background-color: rgb(0, 89, 255);
}
#main .clickable:hover{
    cursor: pointer;
}
#playlist .header {
  min-width: 100%;
  height: 7rem;
  color: white;
  font-size: 6rem
}
#playlist .content {
  padding-top: 4rem;
  font-size: 1.2rem;
}

#playlist .section-header {
  border-bottom: 0.5px solid white;
}
#playlist .section-content{
  font-size: 1rem;
  color: rgb(165, 162, 162);
}
#playlist img{
  height: 225px;
  width: 225px;
  
}
#playlist .clickable:hover{
  cursor: pointer;
}
#song .table-item{
    font-size: 18px;
    border-bottom: 1px solid rgb(161, 161, 161);
    color: rgb(216, 216, 216);
}
#song .table-header{
    border-bottom: 2px solid rgb(161, 161, 161);
    font-size: 25px;
}
#song .table-item .function{
    color: rgb(39, 39, 39);
}
#song .table-item:hover .function{
    color: white;
}  
#song .table-item:hover{
    background-color: rgb(20, 20, 20);
    color:white;
}
#song .header{
    display: block;
    min-width: 100%;
    height: 5rem;
    font-size: 4rem;
}
